import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from 'angularfire2/firestore';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Component, OnInit} from "@angular/core";
import * as firebase from 'firebase/app';
import {isStringLiteralLike} from "codelyzer/util/utils";

interface todos {

  UID : string;
  description: string;
  done: boolean;
  color: string;

}

interface todo extends todos {
  id: string;

}

@Component({
  selector: 'app-root',
  templateUrl: './todos.component.html',
  styleUrls: ["app.component.css"]
})
export class TodosComponent implements OnInit {
  description: string;
  todosCollection: AngularFirestoreCollection<todos>;
  todos: Observable<{ id: string; data: todo }[]>;

  constructor(
    private afs: AngularFirestore,

) {

  }

  ngOnInit() {
    var user = firebase.auth().currentUser.uid;
    this.todosCollection = this.afs.collection('todos');
    this.todos = this.todosCollection.snapshotChanges()
      .map(actions => {
        return actions
          .map(a => {
          const data = a.payload.doc.data() as todo;
          const id = a.payload.doc.id;
          return {id, data};
        }).filter(data => data.data.UID == user);
      });
  }

  addTodo() {
    var user = firebase.auth().currentUser.uid;
    this.afs.collection('todos').add({
      'description': this.description,
      'done': false,
      'color': '',
      'UID': user
    });

  }

  updateTodo(id, event) {
    const checkedVal = event.target.checked;
    this.afs.doc('todos/' + id).update({
      'done': checkedVal
    });
  }
  removeTodo(id){
    this.afs.doc('todos/' + id).delete();
  }

  updateColor(id, color) {
    this.afs.doc('todos/' + id).update({
      'color': color
    });
  }

}
